export default {
    data() {
        return {
            search: {
                page: 1,
                page_size: 10,
                start_time: '',
                end_time: '',
                site_id: ""
            },
            radio: "",
            dateSpace: [],
            domainArr: [],
            tableData: [],
            total: 0
        }
    },
    mounted() {
        this.radio = "1";
        this.getC()
    },
    watch: {
        dateSpace() {
            this.search.start_time = this.dateSpace ? this.dateSpace[0] : "";
            this.search.end_time = this.dateSpace ? this.dateSpace[1] : "";
            // this.getData();
        },
        radio() {
            switch (this.radio) {
                case "1":
                    this.dateSpace = [this.combinationDate(3)[0], this.combinationDate(3)[1]];
                    break;
                case "2":
                    this.dateSpace = [this.combinationDate(7)[0], this.combinationDate(7)[1]];
                    break;
                case "3":
                    this.dateSpace = [this.combinationDate(30)[0], this.combinationDate(30)[1]];
                    break;
            }
        }
    },
    methods: {
        currentChange(e) {
            this.search.page = e;
            this.getData();
        },
        indexhandel(a) {
            let index = a + 1;
            return (this.search.page == 1 ? index : (this.search.page - 1) * this.search.page_size + index)
        },
        getC() {
            this.domainArr = []
            this.axios.get("/v1/site_js/Site_list/index/page1/page_size/1000000").then(res => {
                if (res.data.code == 200) {
                    res.data.result.list.forEach(e => {
                        let obj = {
                            value: e.id,
                            label: e.domain_url
                        }
                        this.domainArr.push(obj)
                    })
                }
            })
        },
        combinationDate(n) {
            let newD = new Date().getTime()
            let lately = newD - (1000 * 60 * 60 * 24 * n);
            return [this.parseDate(lately), this.parseDate(newD)]
        },
        parseDate(pDate) {
            let date = new Date(pDate);
            let year = date.getFullYear();
            let month = date.getMonth() + 1 > 9 ? (date.getMonth() + 1) : "0" + (date.getMonth() + 1);
            let day = date.getDate() > 9 ? date.getDate() : "0" + date.getDate();
            return year + "-" + month + "-" + day
        }
    }
}