<template>
  <div class="wrapperCenter">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item
        >来粉统计 / 控制台 / 转化分析 / 页面分析</el-breadcrumb-item
      >
    </el-breadcrumb>
    <div class="search">
      <div>
        <el-radio v-model="radio" label="1">近3天</el-radio>
        <el-radio v-model="radio" label="2">近7天</el-radio>
        <el-radio v-model="radio" label="3">近30天</el-radio>
      </div>
      <el-date-picker
        class="dateSpace"
        v-model="dateSpace"
        type="daterange"
        range-separator="至"
        value-format="yyyy-MM-dd"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
      >
      </el-date-picker>

      <el-select
        v-model="search.site_id"
        placeholder="请选择网站"
        class="searchDomain"
      >
        <el-option
          v-for="item in domainArr"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        >
        </el-option>
      </el-select>

      <el-button class="searchBtn" @click="searchFn" type="primary"
        >搜索</el-button
      >
    </div>
    <el-table
      :data="tableData"
      style="width: 100%"
      :header-cell-style="{ background: '#F3F4F7', color: '#555' }"
    >
      <el-table-column
        type="index"
        label="序号"
        width="100px"
        :index="indexhandel"
      ></el-table-column>
      <el-table-column prop="page_url" label="页面链接"></el-table-column>
      <el-table-column prop="ip_num" label="IP数"></el-table-column>
      <el-table-column prop="uv_num" label="UV数"></el-table-column>
      <el-table-column prop="pv_num" label="浏览量(PV)"></el-table-column>
      <el-table-column prop="change_num" label="转化数"></el-table-column>
      <div slot="empty" class="nodata">
        <img src="@/assets/noHaveData.jpg" alt="" />
        <p>暂无数据</p>
      </div>
    </el-table>
    <el-pagination
      v-if="tableData.length > 0"
      :page-size="search.page_size"
      :current-page.sync="search.page"
      background
      layout="prev, pager, next"
      :total="total"
      @current-change="currentChange"
    ></el-pagination>
  </div>
</template>

<script>
import utils from "@/mixin/watchDate.js";
export default {
  mixins: [utils],
  data() {
    return {};
  },
  methods: {
    searchFn() {
      this.search.page = 1;
      this.getData();
    },
    getData() {
      this.axios.post("/v1/data/Page_change/index", this.search).then((res) => {
        if (res.data.code == 200) {
          this.tableData = res.data.result.list;
          this.total = res.data.result.page_info.total;
        } else {
          this.$message.error(res.data.message);
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>
.searchBtn {
  margin-left: 20px;
}
.search,
.dateSpace {
  margin: 20px 0;
}
</style>